.navbar {
  display: flex;
  justify-content: space-between;
  padding: 1.5rem 0 1.5rem 0;
}

.header {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 15%;
}

.header img {
  height: 5rem;
  width: auto;
}

.btnPrimary {
  background-color: #FFCB05;
  border: 2px solid #422800;
  border-radius: 30px;
  box-shadow: #422800 4px 4px 0 0;
  color: #422800;
  cursor: pointer;
  display: inline-block;
  font-weight: 600;
  font-size: 18px;
  padding: 0 18px;
  line-height: 50px;
  text-align: center;
  text-decoration: none;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  height: 50px;
  line-height: 50px;
}

.btnPrimary:hover {
  background-color: #fff;
}

.btnPrimary:active {
  box-shadow: #422800 2px 2px 0 0;
  transform: translate(2px, 2px);
}

@media (min-width: 768px) {
  .btnPrimary {
    min-width: 120px;
    padding: 0 25px;
    height: 50px;
    line-height: 50px;
  }
}

.buttonContent {
  padding: 20px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 5px;
  align-items: center;
}

/* Responsive Styles */
@media (max-width: 767px) {
  .navbar {
    flex-direction: column;
    align-items: center;
  }

  .header {
    margin-bottom: 20px;
  }

  .buttonContent {
    flex-wrap: wrap;
    justify-content: center;
  }

  .btnPrimary {
    margin-bottom: 10px;
    height: 40px; /* Ajusta la altura deseada */
    line-height: 40px; /* Ajusta la altura deseada */
  }
}
